.gallery {
  padding: 25px 20px;

  @media (min-width: @tablet-width) {
    padding: 0 60px;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
  }
}

.gallery__list {
  list-style-type: none;
  margin: 0;
  padding-left: 0;

  display: flex;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.card__link {
  text-decoration: none;
}

.card__title {
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
}

.card__author {
  color: #333333;
}

.card__results {
  font-weight: 700;
}
